@import url(https://fonts.googleapis.com/css?family=Noto+Sans+TC:400,700&display=swap);
* {
    font-family: 'Taipei Sans TC Beta', 'Noto Sans TC', sans-serif;
    font-weight: bold;
    box-sizing: border-box;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.App {
    width: 1920px;
    height: 1080px;
}

.content {
    margin: 65px auto 69px auto;
}
.header {
    background: #D7E8F6;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.steps {
    display: flex;
    width: calc(100% - 596px * 2);
    justify-content: space-between;
    font-size: 22px;
    font-weight: bold;
}

.step {
    color: #999999;
    display: flex;
    align-items: center;
}

.step img {
    display: inline-block;
    width: 45px;
    height: 45px;
}

.step.active {
    color: #5CADE1;
}

.step__index {
    display: inline-block;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    border: 2px dashed #999999;
    background: #FFFFFF;
    text-align: center;
    line-height: 41px;
}

.step.active .step__index {
    border: 2px solid #5CADE1;
    background: #5CADE1;
    color: #FBFCFE;
}

.step__name {
    display: inline-block;
    margin-left: 11px;
}
.content {
    margin: 65px 140px 69px 140px;
}

.plans {
    margin-top: 27px;
    margin-bottom: 77px;
    display: flex;
    justify-content: space-between;
}

.title {
    display: flex;
    align-items: center;
}

.title__name {
    line-height: 38px;
    font-size: 38px;
    font-weight: bold;
    color: #333333;
    vertical-align: middle;
}

.title__rect {
    background: #57C1E7;
    width: 10px;
    height: 32px;
    margin-right: 15px;
}

.payments {
    margin-top: 27px;
    margin-bottom: 135px;
    display: flex;
    justify-content: space-between;
    font-size: 32px;
    font-weight: bold;
    color: #5CADE1;
}

.plan {
    width: 398px;
    height: 327px;
    border-radius: 5px;
    background: #EBF5FB;
    display: flex;
    justify-content: center;
    align-items: center;
}

.plan img {
    display: flex;
}

.plan.selected {
    background: #5CADE1;
}
.payment {
    width: 398px;
    height: 120px;
    border-radius: 5px;
    background: #EBF5FB;
    display: flex;
    justify-content: center;
    align-items: center;
}

.payment img {
    display: flex;
    margin-right: 18px;
}

.payment.selected {
    background: #5CADE1;
}
.actions {
    display: flex;
    justify-content: space-between;
    padding: 0 23px;
    font-size: 32px;
    color: #333333;
}

.action {
    width: 80px;
}

.action:hover {
    cursor: pointer;
}

.action__name {
    margin-bottom: 13px;
    text-align: center;
}

.action__bar {
    width: 100%;
    height: 11px;
    border-radius: 10px;
    background: #D7E8F6;
}


.credit {
    display: flex;
    justify-content: space-between;
}

.form {
    margin-top: 52px;
    width: 566px;
}

.row {
    width: 100%;
    font-size: 25px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    flex-wrap: wrap;
    margin-bottom: 40px;
}

.field {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.field__name {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
}

.credit__type {
    display: flex;
    justify-content: flex-end;
}

.credit__type img {
    margin-left: 15.5px
}

.field__input {
    width: 100%;
    height: 55px;
    margin-top: 17px;
    border: 2px solid #333333;
    border-radius: 5px;
}

.expire {
    width: 245px;
}

.cvc {
    width: 245px;
}

.owner {
    width: 100%;
}

.pwd {
    width: 213px;
}

.sms-pwd {
    width: 305px;
    height: 55px;
    border-radius: 28px;
    font-size: 25px;
    font-weight: normal;
    color: #333333;
    background: -webkit-gradient(linear, left top, left bottom, from(#FBFCFE), color-stop(#EBF5FB), to(#D7E8F6));
    background: -webkit-linear-gradient(top, #FBFCFE, #EBF5FB, #D7E8F6);
    background: linear-gradient(to bottom, #FBFCFE, #EBF5FB, #D7E8F6);
}

.remark {
    width: 100%;
    margin-top: 29px;
    font-size: 18px;
    font-weight: normal;
    color: #999999;
}

.credit__plan {
    margin-top: 65px;
    width: 398px;
    height: 579px;
    border-radius: 5px;
    background: #EBF5FB;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.credit__plan-name {
    margin-top: 30.5px;
    margin-bottom: 20px;
    font-size: 25px;
    font-weight: bold;
}

.credit img {
    display: block;
    width: 170px;
    height: 224px;
    margin-top: 50px;
    margin-bottom: 36.5px;
}

.credit__attendtion {
    width: 327px;
}

.credit__attendtion li {
    font-size: 18px;
    font-weight: normal;
}

.line {
    width: 348px;
    height: 2px;
    background: #333333;
}
.finish {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 124px;
}

.finish__message {
    width: 100%;
    text-align: center;
    font-size: 50px;
    color: #5CADE1;
    margin-top: 69px;
    margin-bottom: 29px;
}

.finish__btn {
    width: 335px;
    height: 101px;
    color: #5CADE1;
    font-size: 32px;
    background: #EBF5FB;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

